/**
 * 1.异步更新state,短时间内把多个setState合并成一个(队列)
 * 2.一段时间之后 循环清空队列 渲染组件
 * 3.
 */
import { renderComponent } from '../react-dom'
const setStateQueue = []
const renderQueue = []
    // 保存当前正在更新状态的组件
export function enqueueSetState(stateChange, component) {
    if (setStateQueue.length === 0) {
        setTimeout(() => {
            flush()
        }, 0)
    }
    // 短时间内 合并多个setState
    setStateQueue.push({ stateChange, component })
        // 如果renderQueue 中没有当前组件 就把组件添加进 组件队列
    let r = renderQueue.some(item => {
        return item === component
    })
    if (!r) {
        // 证明是第一次添加
        renderQueue.push(component)
    }
}
// 一段时间之后
function flush() {
    let item;
    while (item = setStateQueue.shift()) {
        const { stateChange, component } = item

        // 保存之前的状态
        if (!component.preveState) {
            component.preveState = Object.assign({}, component.state)
        }

        if (typeof stateChange === 'function') { // 传进来的是一个回调函数
            Object.assign(component.state, stateChange(component.preveState, component.props))
        } else { // 否则就是传进来的是一个对象 肯定是同步的
            Object.assign(component.state, stateChange)
        }
    }
    let component;
    while (component = renderQueue.shift()) {
        renderComponent(component)
    }
}