import React from './React'
import ReactDOM from './react-dom'
const ele = (<div className="active" title="123">hellow, <span>react</span></div>)

// function Home() {
//     return (<div className="active" title="123">hellow, <span>react 函数组件</span></div>)
// }

class Home extends React.Component {
    constructor (props) {
        super(props)
        this.props = props
        this.state = {
            num:0
        }
    }
    componentWillMount () {
      console.log('组件将要加载')
    }
    componentWillReceiveProps (props) {
      console.log('组件将要接受参数')
    }
    componentWillUpdate () {
      console.log('组件将要更新')
    }
    componentDidUpdate() {
      console.log("组件更新完成")
    }
    componentDidMount () {
      console.log("组件加载完成")
    }
    handler () {
        this.setState({
            num:this.state.num +1
        })
    }
    render () {
        return (<div className="active" title="123">hellow, 
            <span>react 类组件 {this.state.num}</span>
            <button onClick={this.handler.bind(this)}>摸我</button>
        </div>)
    }
}
ReactDOM.render(<Home />,document.querySelector("#root"));
